@font-face {
  font-family: 'Molde Semi Condensed';
  src: local('Molde Semi Condensed Semibold'),
    local('Molde-Semi-Condensed-Semibold'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.woff2') format('woff2'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.woff') format('woff'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

:root {
  --main-font: 'Inter';
  --primary-color: #14096d;
  --secondary-color: #fc7a1e;
  --text-color: #485696;
}
/* css resert  */
/* 
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;

  min-height: 100vh;
}
/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  8. Create a root stacking context
*/

